.notified {
  padding: 40px 10px 80px 10px;
  background-color: #2b44ff;
  border-radius: 70px 70px 70px 70px;
}

.notified-header {
  font-family: "Roboto Slab" !important;
  color: #ffffff;
  text-align: center;
  font-size: 50px;
  font-weight: bold;
  margin-bottom: 20px;
}

.notified-form {
  margin: auto;
  width: 90%;
  padding: 20px 40px;
  text-align: center;
  background-color: #ffffff;
  -webkit-box-shadow: 25px 25px 60px 0px rgba(16, 16, 16, 0.41);
  -moz-box-shadow: 25px 25px 60px 0px rgba(16, 16, 16, 0.41);
  box-shadow: 25px 25px 60px 0px rgba(16, 16, 16, 0.41);
}

.notified-form-field {
  background-color: #f9f9f9;
  border: 2px solid #707070 !important;
  border-radius: 100em !important;
  padding: 12px 10px !important;
  margin: 0 0 18px 0;
  text-align: center;
  color: #150578;
  font-family: "Roboto Slab" !important;
  font-size: 20px !important;
}

.notified-form-btn {
  background-color: #2b44ff !important;
  border: 1px solid #707070 !important;
  border-radius: 100em !important;
  padding: 12px 10px !important;
  text-align: center;
  width: 100%;
  color: #ffffff !important;
  font-family: "Roboto Slab" !important;
  font-size: 20px !important;
  margin: 10px 0 40px 0 !important;
}

.notified-rt-div {
  height: 600px;
  background-image: url("../../Assets/notified-img.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.notified-text {
  font-family: "Roboto Slab" !important;
  font-size: 35px !important;
  color: #ffffff;
  padding: 15px 150px;
}
