.testimonial {
  background-color: #ffffff;
  margin: 40px 0;
}

.testimonial-header {
  font-family: "Roboto Slab" !important;
  text-align: center;
  font-weight: bold;
  font-size: 50px;
}

#quote-carousel {
  padding: 0 10px 30px 10px;
  margin-top: 40px;
}

#quote-carousel .carousel-indicators {
  position: relative;
  right: 50%;
  top: auto;
  bottom: 0px;
  margin: 40px 30px;
}

#quote-carousel .carousel-indicators li {
  width: 70px;
  height: 70px;
  cursor: pointer;
  border: 5px solid #ffffff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  opacity: 0.4;
  overflow: hidden;
  transition: all 0.4s ease-in;
  vertical-align: middle;
  margin: 0 30px;
}

#quote-carousel .carousel-indicators li img {
  display: block;
}

#quote-carousel .carousel-indicators .active {
  width: 100px;
  height: 100px;
  border: 5px solid #2765f6;
  opacity: 1;
  transition: all 0.2s;
}

.quote {
  font-family: "Roboto Slab" !important;
  font-size: 35px !important;
  margin: 30px 0;
}

.double-quotes {
  margin: 30px 40px;
}
